import {withRouter} from 'next/router'
import styled from 'styled-components';
import Link from 'next/link';
import Layout from '../components/MyLayout.js'

const StyledNav = styled.nav`
  background-color: red;
`

const StyledHeader = styled.header`
  background-color: red;
`

class PokedexEntry extends React.Component {

  static async getInitialProps({ query }) {
    let data = {};
    try {
      data = await(await fetch(`https://pokeapi.co/api/v2/pokemon/${query.name}`)).json();
    } catch(err) {
      console.log(err);
    }
    return data;
  }

  render() {
    console.log(this.props);
    if(!this.props.name) {
      return (
        <h1>404 Page not found</h1>
      )
    } else {
      return (
        <Layout>
          <StyledNav>
            <Link>prev</Link>
            <Link>home</Link>
            <Link>next</Link>
          </StyledNav>
          <StyledHeader>{this.props.name}</StyledHeader>
          <img src={this.props.sprites.front_default} />
          <p>this is the pokedex entry for {this.props.name}</p>
        </Layout>
      )
    }
  }
}

export default PokedexEntry;
